@media screen and (max-width: 800px) {
  .about-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .about-text {
    max-width: 80vw;
  }

  .about-photo img {
    max-width: 80vw;
  }
}

@media screen and (min-width: 801px) and (max-width: 1200px) {
  .about-page {
    display: flex;
    flex-direction: row;
    gap: 64px;
    justify-content: center;
    align-items: center;
  }
  .about-text {
    width: 35vw;
    max-width: 360px;
  }

  .about-photo img {
    width: 35vw;
    max-width: 360px;
  }
}

@media screen and (min-width: 1201px) {
  .about-page {
    display: flex;
    flex-direction: row;
    gap: 64px;
    justify-content: center;
    align-items: center;
  }
  .about-text {
    min-width: 360px;
    width: 30vw;
  }

  .about-photo img {
    min-width: 360px;
    width: 30vw;
  }
}

.about-photo img {
  object-fit: contain;
  margin: 24px;
}

.about-text h1 {
  color: #002649;
  font-family: IBM Plex Sans;
  font-size: 3rem;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.about-text p {
  color: #002649;
  font-family: IBM Plex Sans;
  font-size: 1.2rem;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.social svg {
  fill: #2a75e5;
  transition: all 0.1s ease-in;
}

.social svg:hover {
  fill: #0f3165;
  cursor: pointer;
}

.thankyou {
  font-family: IBM Plex Sans;
  font-size: 10px;
  padding: 12px 0px;
}
